html{
	font-size: 16px;
}
body {
	background-color: white !important;
}
::-webkit-input-placeholder {
	/* Chrome/Opera/Safari */
	color: rgba(0, 0, 0, 0.87);
}
::-moz-placeholder {
	/* Firefox 19+ */
	color: rgba(0, 0, 0, 0.87);
}
:-ms-input-placeholder {
	/* IE 10+ */
	color: rgba(0, 0, 0, 0.87);
}
:-moz-placeholder {
	/* Firefox 18- */
	color: rgba(0, 0, 0, 0.87);
}
@media print {
	.hidden {
		display: none !important;
	}
}
@media print {
	@page {
		size: landscape;
		@bottom-center {
			content: counter(page) ' of ' counter(pages);
		}
	}
}
